import {Component,ViewChild } from '@angular/core';
import { ChildComponent } from './child.component';

@Component(
    {
        selector:'app-parent',
        template:`<h3>value:{{value}}</h3>
        <app-child (eventClick)="changeValue($event)"></app-child>
        <button (click)="onAddForChild();">Add for child</button>
        <app-child></app-child>`
    }
)

export class ParentComponent{

    @ViewChild(ChildComponent)
    myChild: ChildComponent;

    onAddForChild(){
        this.myChild.value++;
        this.myChild.giatri=this.myChild.giatri+2;
    }
value =0;

changeValue(isAdd: boolean){
    if(isAdd)
        this.value=this.value+1;
    else
        this.value=this.value-1;
}

}


