import { Component, OnInit } from '@angular/core';
import { WeatherService } from './weather.service';
@Component({
    selector: 'app-weather',
  templateUrl: './weather.component.html',
  styleUrls: ['./weather.component.css'],
  providers:[WeatherService]
})
export class WeatherComponent implements OnInit {
txtctyName='';
ctyName='';
t=0;
isLoading=false;

  constructor(private weatherService: WeatherService) { }

  ngOnInit() {
    this.weatherService.GetIp('hanoi')
    .then(w=>console.log(w))
    .catch(err=>console.log(err));
  }
getWeather(){
  this.isLoading=true;
this.weatherService.GetIp(this.txtctyName)
.then(temp=>{
  this.ctyName=this.txtctyName;
  this.t=temp;
  this.isLoading=false;
})
.catch(err=>
{
  alert('Cannot find your city name!');
  this.isLoading=false;
  this.txtctyName='';
  this.ctyName='';
});
}

getMessage(){
  if(this.isLoading) return 'Loadding...';

  return this.ctyName ==='' ? 'Enter your city Name': (this.ctyName+ ' is now ' + this.t)
  }
}



