import { Component, EventEmitter, Output } from '@angular/core';

@Component(
    {
        selector:'app-child',
        template:`
        <h3>chilvalue:{{value}}</h3>
        <h1>gia tri:{{giatri}}</h1>
        <button (click)="addForParent()">add</button>
        <button (click)="subForParent()">Sub</button>
        
        `
    }
)

export class ChildComponent{
    value=0;
    giatri=0;
    @Output() eventClick=new EventEmitter<boolean>();
    addForParent(){
        this.eventClick.emit(true);
    }
    subForParent(){
        this.eventClick.emit(false);
    }
}


